    import {
        mapState,
        mapMutations
    } from 'vuex';
    import store from "../../store/index.js";
    import api from "../../api/api.js";
    export default {
        computed: {
			...mapState(['hasLogin', 'userInfo', 'token']),
			pointText() {
				if (this.userInfo.points){
					if (this.userInfo.points > 100000000){
						return (this.userInfo.points/10000000).toFixed(1) + '千万';
					}
					else if (this.userInfo.points > 1000000){
						return (this.userInfo.points/1000000).toFixed(1) + '百万';
					}
					else if (this.userInfo.points > 10000){
						return (this.userInfo.points/1000).toFixed(1) + '千';
					}
					else {
						return this.userInfo.points;
					}
				}
				return 0;
			}
		},
        data() {
            return {}
        },
        onPullDownRefresh: function() {
            if (this.hasLogin) {
                this.reFresh();
                uni.stopPullDownRefresh();
            } else {
                uni.stopPullDownRefresh();
                return;
            }
        },
        onShow() {
            if (this.hasLogin) {
                this.reFresh();
            } else {
                return;
            }
        },
        methods: {
            bindLogin() {
                if (this.hasLogin) {
                    this.logout()
                } else {
                    uni.navigateTo({
                        url: 'login/login'
                    })
                }
            },
            goLogin() {
                if (!this.hasLogin) {
                    uni.navigateTo({
                        url: 'login/login'
                    })
                }
            },
            goSetting() {
                uni.navigateTo({
                    url: 'setting/setting'
                })
            },
            goMessage() {
                if (this.hasLogin) {
                    uni.reLaunch({
                        url: '../../pages/messages/messages'
                    })
                }
            },
            goPoints() {
                if (this.hasLogin) {
                    // console.log("in2")
                    // uni.reLaunch({
                    //     url: '../../pages/my/point'
                    // })
                }
            },
            reFresh() {
                api.req.getUserInfo()
            },
            goFollow() {
                if (this.hasLogin) {
                    uni.navigateTo({
                        url: 'my-follows/my-follows' //关注界面路径
                    });
                }
            },
            goCollect() {
                if (this.hasLogin) {
                    uni.navigateTo({
                        url: 'my-collects/my-collects' //收藏界面路径
                    });
                }
            },
            goPost() {
                if (this.hasLogin) {
                    uni.navigateTo({
                        url: 'my-post/my-post' //我的帖子界面路径
                    });
                }
            },
            goComment() {
                if (this.hasLogin) {
                    uni.navigateTo({
                        url: 'my-comments/my-comments' //我的评论界面路径
                    });
                }
            },
            goEditInformation() {
                if (this.hasLogin) {
                    uni.navigateTo({
                        url: '../../pages/my/setting/change-userInfo/change-userInfo' //我的评论界面路径
                    });
                }
            },
            goOther() {
                // uni.navigateTo({
                //     url: '../../pages/otherUsers/otherUsers'
                // })
            },
            ...mapMutations(['login'])
        }
    }
